import React, { createContext, useState, useContext, useEffect } from 'react';

// Создаем контекст авторизации
const AuthContext = createContext(null);

// Ключи для хранения данных в localStorage
const AUTH_STORAGE_KEY = 'hr_partner_auth';
const USERS_STORAGE_KEY = 'hr_partner_users';

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  // Инициализация состояния из localStorage при загрузке
  useEffect(() => {
    const storedAuth = localStorage.getItem(AUTH_STORAGE_KEY);
    if (storedAuth) {
      try {
        const parsedAuth = JSON.parse(storedAuth);
        setUser(parsedAuth.user);
      } catch (error) {
        console.error('Ошибка при парсинге данных авторизации:', error);
        localStorage.removeItem(AUTH_STORAGE_KEY);
      }
    }
    
    // Проверяем, существуют ли зарегистрированные пользователи
    const storedUsers = localStorage.getItem(USERS_STORAGE_KEY);
    if (!storedUsers) {
      // Если пользователей нет, создаем админа по умолчанию
      const defaultUsers = [
        {
          id: '1',
          email: 'admin@example.com',
          password: 'password',
          name: 'Администратор',
          role: 'admin'
        }
      ];
      localStorage.setItem(USERS_STORAGE_KEY, JSON.stringify(defaultUsers));
    }
    
    setLoading(false);
  }, []);

  // Получение списка зарегистрированных пользователей
  const getUsers = () => {
    const storedUsers = localStorage.getItem(USERS_STORAGE_KEY);
    return storedUsers ? JSON.parse(storedUsers) : [];
  };

  // Функция для входа пользователя
  const login = async (email, password) => {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const users = getUsers();
        const foundUser = users.find(
          user => user.email === email && user.password === password
        );
        
        if (foundUser) {
          // Создаем копию пользователя без пароля для безопасности
          const userData = { ...foundUser };
          delete userData.password;
          
          // Сохраняем данные авторизации
          const authData = {
            user: userData,
            token: 'mock-jwt-token'
          };
          
          // Сохраняем в localStorage
          localStorage.setItem(AUTH_STORAGE_KEY, JSON.stringify(authData));
          
          // Обновляем состояние
          setUser(userData);
          
          resolve(userData);
        } else {
          reject(new Error('Неверный email или пароль'));
        }
      }, 1000);
    });
  };

  // Функция для регистрации
  const register = async (userData) => {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const users = getUsers();
        
        // Проверка, существует ли пользователь с таким email
        const existingUser = users.find(user => user.email === userData.email);
        if (existingUser) {
          reject(new Error('Пользователь с таким email уже существует'));
          return;
        }
        
        // Создаем нового пользователя
        const newUser = {
          id: String(users.length + 1),
          email: userData.email,
          password: userData.password,
          name: userData.name || userData.email.split('@')[0],
          role: 'user'
        };
        
        // Добавляем пользователя в список
        users.push(newUser);
        localStorage.setItem(USERS_STORAGE_KEY, JSON.stringify(users));
        
        // Успешная регистрация
        resolve({ success: true });
      }, 1000);
    });
  };

  // Функция для выхода пользователя
  const logout = () => {
    // Удаляем данные из localStorage
    localStorage.removeItem(AUTH_STORAGE_KEY);
    // Сбрасываем состояние
    setUser(null);
  };

  // Проверка авторизации
  const isAuthenticated = !!user;

  // Значение контекста
  const value = {
    user,
    loading,
    isAuthenticated,
    login,
    register,
    logout
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

// Хук для использования контекста авторизации
export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth должен использоваться внутри AuthProvider');
  }
  return context;
};

export default AuthContext; 